class Locker {
    constructor() {
        this.lockCount = 0;
    }
    get IsLocked() {
        return this.lockCount > 0;
    }
    lock() {
        this.lockCount += 1;
    }
    unlock() {
        if (!this.IsLocked)
            return;
        this.lockCount -= 1;
    }
    reset() {
        this.lockCount = 0;
    }
    lockOnce() {
        if (!this.IsLocked)
            this.lock();
    }
    doLockedAction(action) {
        this.lock();
        try {
            action();
        }
        finally {
            this.unlock();
        }
    }
    async doLockedAsyncAction(asyncAction) {
        this.lock();
        try {
            await asyncAction();
        }
        finally {
            this.unlock();
        }
    }
    doIfNotLocked(action) {
        if (!this.IsLocked)
            action();
    }
    doLockedActionIfNotLocked(action) {
        this.doIfNotLocked(() => this.doLockedAction(action));
    }
    async doLockedAsyncActionIfNotLocked(asyncAction) {
        this.doIfNotLocked(async () => await this.doLockedAsyncAction(asyncAction));
    }
}
export { Locker };
